import React from 'react';
import PropTypes from 'prop-types';

import * as Styled from './styles';

const TimelinePublication = ({ content, year }) => (
  <Styled.TimelinePublication>
    <Styled.Point />
    <Styled.Details>
      <Styled.Date>
        {year}
      </Styled.Date>
      <Styled.Subtitle>{content}</Styled.Subtitle>
    </Styled.Details>
  </Styled.TimelinePublication>
);

TimelinePublication.propTypes = {
  content: PropTypes.any.isRequired,
  year: PropTypes.string.isRequired
};

export default TimelinePublication;
